
.banner__content__video__grid {
    @include grid;
}

.banner--video {
    padding-top: $measure-large;
    padding-bottom: $measure-large;

    background: gradient(180deg, #eeeeee, 25%, white) no-repeat;

    background-size: calc(100% - 2 * #{$measure-large}) 100%;

    @include media("above:extralarge") {
        background-size: calc(50% + #{$grid-width-large} / 2 - 2 * #{$measure-large}) 100%;
    }

    .banner__content__text {
        @include grid-column-pull(6);
    }

    .banner__content__ctas {

        margin-top: $measure-enormous;
        a {
            @include anchor(cta);
            @include anchor(cta--inline);
            @include anchor(cta--allports);

            margin: 0 $measure-large $measure-large 0;
        }
    }

}

.banner__content__video {
    @include grid-column(6);
    @include grid-column-push(6);
}

