////
/// Pathways Cards
/// @group components
////


.pathway-card {
    background-color: $color-lightgray;
    color: $color-black;
    width: 100%;
    min-height: 12rem;
    display: block;
    border-bottom: 0;
    margin-bottom: $measure-large;
    overflow: hidden;
    position: relative;
    text-align: center;

    @supports (display: flex) {
        display: flex;
        flex-direction: row;
    }

    @include media("below:medium") {
        @supports (display: flex) {
            flex-wrap: wrap;
        }
    }

    img {
        max-width: 50%;
        height: auto;
        align-self: center;
        display: block;
        padding: 0;

        @include media("below:medium") {
            display: none;
        }

        &.pathway-card__full {
            filter: brightness(25%);
            width: 100%;
            max-width: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) translateY(-50%);
            z-index: z-index(default);

            @include media("below:medium") {
                display: block;
                width: 100%;
                max-width: auto;
            }
        }
    }
}

[class*="pathway-card--"] {
    border-bottom-width: 0;

    &:before {
        @include transition(border-color);
        content: "";
        border: $border-width-hairline $border-style-default rgba($color-white, 0.5);
        position: absolute;
        top:    $measure-medium;
        right:  $measure-medium;
        bottom: $measure-medium;
        left:   $measure-medium;
        pointer-events: none;
        z-index: z-index(above);
    }

    &:hover,
    &:focus,
    &:active {
        background-color: inherit;
        color: inherit;

        &:before {
            z-index: z-index(navigation);
        }
    }

    &:link:hover,
    &:link:focus,
    &:link:active {
        &:before {
            border-color: $color-white;
        }

        %heading-elements {
            text-decoration: underline;
        }
    }
}

.pathway-card__left,
.pathway-card__right,
.pathway-card__full {
    background-color: $color-lightgray;
    display: flex;
    flex-direction: column;
    position: relative;

    %heading-elements,
    p {
        color: $color-black;
        position: relative;
        z-index: z-index(default);
    }

    %heading-elements {
        flex: 1 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 0;
        font-weight: $font-weight-bold;
    }

    p {
        margin-bottom: $measure-medium;
        line-height: $line-height-default;
    }
}

.pathway-card__left,
.pathway-card__right {
    flex: 0 1 50%;

    @include media("below:medium") {
        flex: 1 0 100%;
    }

    %heading-elements,
    p {
        padding: 0 $measure-medium;
    }
}

.pathway-card__left {
    padding: $measure-medium 0 $measure-medium $measure-medium;

    &[class*="pathway-card--"] {
        &:before {
            right: 0;
        }
    }
}

.pathway-card__right {
    padding: $measure-medium $measure-medium $measure-medium 0;

    &[class*="pathway-card--"] {
        &:before {
            left: 0;
        }
    }
}

.pathway-card__full {
    flex: 1 0 100%;
    padding: $measure-medium;

    %heading-elements,
    p {
        color: $color-white;
    }
}



$level-colors: (
    undergraduate: ($color-flamingo, $color-yellow),
    masters: ($color-prussian, $color-prussian),
    mba: ($color-sherpa, $color-sherpa)
);

@each $level, $colors in $level-colors {
    $primary-color:   nth($colors, 1);
    $secondary-color: nth($colors, 2);
    .pathway-card--#{$level} {
        &:before {
            border-color: rgba($secondary-color, 0.5) !important;
        }

        &:link:hover,
        &:link:focus,
        &:link:active {
            &:before {
                border-color: $secondary-color !important;
            }
        }

        &[class*="pathway-card__"],
        [class*="pathway-card__"] {
            background-color: mix($color-white, $primary-color, 90%) !important;
        }
    }
}
