
.banner {
    position: relative;
}

.banner__container {
    @extend .container;
}

.banner__content {

    @include grid-column-map(("small": 12, "extralarge": 10));
    @include grid-column-push-map(("small": 0, "extralarge": 1));

    .banner__content__promo {
        @include media("below:medium") {
            display: none;
        }

        position: absolute;
        z-index: z-index(navigation);
        bottom: -2rem;
        width: 20%;

        left: 0;
        .banner--cutout--left & {
            right: 0;
            left: auto;
        }
    }

    .banner__content__text {
        padding-top: $measure-enormous;

        @include grid-column(6);
    }

    .banner__content__text__item {
        margin-bottom: $measure-small;
    }

    h1,
    .banner__content__text__subtitle {
        display: inline-block;
        margin: 0;
        padding: $measure-small $measure-medium;
        width: auto;
        font-size: $font-size-beta;
        @include font-family(meta);
    }

    .banner__content__text__subtitle,
    .banner__content__text__standfirst {
        font-weight: 300;
    }

    .banner__content__text__standfirst {
        font-size: $font-size-gamma;
    }

    @include media("below:medium") {
        .banner__content__text__subtitle,
        .banner__content__text__standfirst {
            display: none;
        }
    }

    h1 {
        font-weight: 500;
    }

    $themes: (
            corporate: #D92231 $color-white #D0CFCB $color-black,
            undergraduate: #F15F22 $color-black #FBAE3B $color-black,
            postgraduate: #00284D $color-white #0077a0 $color-white,
            phd: #0077a0 $color-white #49C7ED $color-black,
            mba: #00444D $color-white #88d1CE $color-black,
            emba: #39655D $color-white #88d1CE $color-black,
            demba: #39655D $color-white #86C5B3 $color-black,
        //demba: #86C5B3 $color-white #88d1CE $color-black,
    );

    @each $theme, $colors in $themes {
        .banner--theme--#{$theme} & {
            h1 {
                background-color: nth($colors, 1);
                color: nth($colors, 2);
            }
            .banner__content__text__subtitle {
                background-color: nth($colors, 3);
                color: nth($colors, 4);
            }
        }
    }
}

// standfirst at bottom of banner:
.banner__standfirst {
    @extend .container;
}

.banner__standfirst__grid {
    @extend .grid;
}

.banner__standfirst__content {
    @include grid-column-map(("small": 12, "medium": 10, "extralarge": 8));
    @include grid-column-push-map(("small": 0, "medium": 1, "extralarge": 2));

    @include font-family(meta);
    font-size: $font-size-gamma;
    color: $color-emperor;
    font-weight: $font-weight-light;
    text-align: center;
}
